/* General Reset */
body {
  margin: 0;
  font-family: "Arial", sans-serif;
  background-color: #f9f9f9;
  color: #333;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Navbar */
.navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #4caf50;
  color: white;
  padding: 15px 30px;
}

.navbar .logo {
  font-size: 24px;
  font-weight: bold;
}

.navbar nav a {
  color: white;
  text-decoration: none;
  margin-left: 15px;
  font-size: 16px;
}

.navbar nav a:hover {
  text-decoration: underline;
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 50px 20px;
  background-color: #eaf7ea;
  width: 100%;
}

.hero h1 {
  font-size: 36px;
  margin-bottom: 10px;
}

.hero p {
  font-size: 18px;
  margin-bottom: 20px;
  color: #555;
}

.primary-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
}

.primary-button:hover {
  background-color: #45a049;
}

/* Booking Section */
.booking-section {
  padding: 40px 20px;
  text-align: center;
  width: 100%;
}

.booking-section h2 {
  margin-bottom: 20px;
  font-size: 28px;
  color: #333;
}

.training-cards {
  display: flex;
  justify-content: center;
  gap: 20px;
  flex-wrap: wrap;
}

.card {
  background: white;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  width: 280px;
  padding: 20px;
  text-align: left;
}

.card h3 {
  font-size: 20px;
  margin-bottom: 10px;
}

.card p {
  color: #666;
  margin-bottom: 15px;
}

.card button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.card button:hover {
  background-color: #45a049;
}

/* Footer */
footer {
  text-align: center;
  background-color: #333;
  color: white;
  padding: 15px;
  width: 100%;
  margin-top: 20px;
  font-size: 14px;
}
